<template>
  <v-container fluid>
    <v-card
      max-width="auto"
      class="mx-auto"
    >
      <v-card-title>个人资料</v-card-title>
      <v-divider />
      <v-card-text>邮箱：{{ userform.username }}</v-card-text>
      <v-card-text>是否订阅:<v-switch v-model="userform.status" :loading="loading" @change="change" :disabled="a"></v-switch></v-card-text>
      <v-form class="form">
        <v-text-field
          v-model="new_pass"
          label="新密码"
          :rules=[rules.required]
          required
        >
        </v-text-field>
        <v-text-field
          v-model="confirm_pass"
          label="确认新密码"
          :rules=[rules.required]
          required
        ></v-text-field>
        <v-btn @click="changePass" :loading="btnLoading">确认修改密码</v-btn>
      </v-form>
      <v-card-text><v-btn @click="logout">退出</v-btn></v-card-text>
    </v-card>
  </v-container>
</template>

<script>
export default {
  name: 'index',
  data () {
    return {
      a: false,
      loading: false,
      btnLoading: false,
      userform:{},
      new_pass: '',
      confirm_pass: '',
      rules: {
          required: value => !!value || '必须填写的内容',
          min: v => v.length >= 6 || 'Min 6 characters',
          emailMatch: () => ('The email and password you entered don\'t match'),
      },
    }
  },
  methods: {
    logout() {
      this.$store.commit('del_token')
      this.$router.push('/login')
    },
    change() {
      const that = this
      this.loading = true
      this.a = true
      this.$axios.put('/api/update_mail_status', that.userform).then(response => {
        that.loading = false
        that.a = false
      }).catch(error => {
        alert('修改失败，大概是服务器炸了')
        that.loading=false
      })
    },
    changePass() {
      const that = this
      this.btnLoading = true
      if (this.new_pass === this.confirm_pass) {
        this.userform['password'] = this.new_pass
        console.log(this.userform)
        this.$axios.put('/api/update_password', that.userform).then(response => {
            alert('修改成功')
          that.btnLoading = false
        }).catch(error => {
          alert('修改失败，大概是服务器炸了')
          that.btnLoading = false
        })
      } else {
        alert('请确保两次输入的一样')
        this.btnLoading = false
      }
    }
  },
  created() {
    this.$axios.get("/").then(response => {
      this.userform = response.data
    }).catch(error => {
      console.log(error)
      this.$Message.error(error)
    })
  }
}
</script>

<style>
.form {
  padding: 16px 16px 8px;
}
</style>