<template>
  <v-container fluid>
    <v-card
      max-width="auto"
      class="mx-auto"
    >
      <v-card-title>登陆</v-card-title>
      <v-divider></v-divider>
      <div class="form">
      <v-form ref="form" lazy-validation>
        <v-text-field
          v-model="userform.username"
          :rules="emailRules"
          label="邮箱"
          clearable
          required
        ></v-text-field>
        <v-text-field
          v-model="userform.password"
          :rules=[rules.required]
          :append-icon="show ? 'visibility' : 'visibility_off'"
          label="密码"
          :type="show ? 'text' : 'password'"
          @click:append="show = !show"
          clearable
        ></v-text-field>

        <v-col>
        <v-row justify="start" align="center">
          <v-btn class="ma-2" :loading="loading" outlined color="indigo" @click="submit">
          登陆
          </v-btn>
          <v-btn class="ma-2" outlined color="indigo" @click="resetform">
          重置
          </v-btn>
        </v-row>
        </v-col>
      </v-form>
      </div>
    </v-card>
  </v-container>
</template>

<script>
  export default {
    name: 'login',
    data () {
      return {
        loading: false,
        show: false,
        userform: {
          username: "",
          password: "",
        },
        rules: {
          required: value => !!value || '必须填写的内容',
          min: v => v.length >= 6 || 'Min 6 characters',
          emailMatch: () => ('The email and password you entered don\'t match'),
        },
        emailRules: [
        v => !!v || '必须填写的内容',
        v => /.+@.+\..+/.test(v) || '邮箱格式错误',
        ],
      }
    },
    methods: {
      resetform () {
        this.$refs.form.reset()
      },
      submit () {
        const that = this
        this.loading=true
        if (this.$refs.form.validate()) {
          this.$axios.defaults.auth = {
            username: that.userform.username,
            password: that.userform.password,
          }
          this.$axios.get('/api/login').then(response => {
            let data = response.data
            // 保存token
            this.$store.commit('set_token', data)
            this.$router.push('/')
          }).catch(error => {
            alert('密码或用户名错误或服务器炸了')
            that.loading=false
          })
        } else {
          alert('表单验证不通过')
          that.loading=false
        }
      }
    }
  }
</script>
<style>
  .form{
    padding: 16px 16px 8px;
  }
</style>